import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import {
  ClienteService,
  Cliente,
  ErroDTO,
  ErroGeral,
} from 'src/app/modules/shared';
import { Subscription } from 'rxjs';
import { ActivatedRoute } from '@angular/router';
import Swal from 'sweetalert2';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-atualizar',
  templateUrl: './atualizar.component.html',
  styleUrls: ['./atualizar.component.css'],
})
export class AtualizarComponent implements OnInit {
  private subscriptions: Subscription[] = [];

  erroGeral = {} as ErroGeral;

  erroDTO = {} as ErroDTO;

  cliente = {} as Cliente;

  clienteNovo = {} as Cliente;

  form: FormGroup;

  clienteId: string;

  constructor(
    private fb: FormBuilder,
    private spinner: NgxSpinnerService,
    private route: ActivatedRoute,
    private clienteService: ClienteService,
    private toast: ToastrService,
  ) {}

  ngOnInit(): void {
    this.criarForm();
    this.clienteId = this.route.snapshot.paramMap.get('clienteid');
    this.carregaDadosCliente();
  }

  carregaDadosCliente(): void {
    this.subscriptions.push(
      this.clienteService.findById(Number(this.clienteId)).subscribe(
        (data) => {
          this.cliente = data;
          this.populandoFomulario();
        },
        (err) => console.log(err),
      ),
    );
  }

  criarForm(): void {
    this.form = this.fb.group({
      codigoSupergas: ['', Validators.required],
      nome: ['', Validators.required],
      cnpj: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
      telefone: ['', Validators.required],
      observacoes: ['', Validators.required],
    });
  }

  atualizarrCliente(): void {
    this.spinner.show();

    if (this.form.invalid) {
      this.spinner.hide();
      this.toast.error('Formulrio com campos invalidos', 'Erro!', {
        timeOut: 4000,
      });
      this.toucheCamposFormulario();
      return;
    }

    this.clienteNovo = this.form.value;
    this.clienteNovo.id = Number(this.clienteId);

    this.subscriptions.push(
      this.clienteService.update(this.clienteNovo).subscribe(
        (data) => {
          const msg = 'Cliente atualizado com sucesso!';
          this.spinner.hide();

          Swal.fire({
            title: msg,
            icon: 'success',
            showConfirmButton: false,
            timer: 3000,
          });
          setTimeout(() => {
            this.refresh();
          }, 2900);
        },
        (err) => console.log(err),
      ),
    );
    this.spinner.hide();
  }

  toucheCamposFormulario(): void {
    this.form.get('codigoSupergas').markAsTouched();
    this.form.get('nome').markAsTouched();
    this.form.get('cnpj').markAsTouched();
    this.form.get('email').markAsTouched();
    this.form.get('telefone').markAsTouched();
    this.form.get('observacoes').markAsTouched();
  }

  populandoFomulario(): void {
    this.form.patchValue({
      codigoSupergas: this.cliente.codigoSupergas,
      nome: this.cliente.nome,
      cnpj: this.cliente.cnpj,
      telefone: this.cliente.telefone,
      observacoes: this.cliente.observacoes,
      email: this.cliente.email,
    });
  }

  refresh(): void {
    window.location.reload();
  }
}
