import { NgModule } from '@angular/core';

import { RouterModule, Routes } from '@angular/router';

import {
  AtualizarComponent,
  CadastrarComponent,
  ListarComponent,
  OrderServiceComponent,
  VisualizarComponent,
} from './components';
import { AutenticaRotaSerivce, AuthGuardService } from '../../shared';

export const OrderServiceRoutes: Routes = [
  {
    path: 'order-service',
    component: OrderServiceComponent,
    canActivate: [AuthGuardService],

    children: [
      {
        path: 'pesquisar',
        component: ListarComponent,
        canActivate: [AutenticaRotaSerivce],
      },
      {
        path: 'cadastrar',
        component: CadastrarComponent,
        canActivate: [AutenticaRotaSerivce],
      },
      { path: 'atualizar/:osid', component: AtualizarComponent },
      { path: 'visualizar/:osid', component: VisualizarComponent },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(OrderServiceRoutes)],

  exports: [RouterModule],
})
export class OrderServiceRoutingModule {}
