import { Component, OnInit } from '@angular/core';
import { Cliente, ClienteService } from 'src/app/modules/shared';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-listar',
  templateUrl: './listar.component.html',
  styleUrls: ['./listar.component.css'],
})
export class ListarComponent implements OnInit {
  private subscriptions: Subscription[] = [];

  clientes: Cliente[] = [];

  clientesFiltrados: Cliente[] = [];

  constructor(private clienteService: ClienteService) {}

  ngOnInit(): void {
    this.carregaDadosclientes();
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach((subscription) => subscription.unsubscribe());
  }

  carregaDadosclientes(): void {
    this.subscriptions.push(
      this.clienteService.getAll().subscribe(
        (data) => {
          this.clientes = data;
          this.clientesFiltrados = this.clientes;
        },
        (err) => console.log(err),
      ),
    );
  }

  filtraClientePeloNome(nome: string): void {
    if (!nome) {
      this.clientesFiltrados = this.clientes;
    } else {
      this.clientesFiltrados = this.clientes.filter((x) =>
        x.nome.trim().toLowerCase().includes(nome.trim().toLowerCase()),
      );
    }
  }
}
