import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Estado, Cidade } from '../../models';
import { API_CONFIG } from '../../config';

@Injectable({
  providedIn: 'root',
})
export class EnderecoService {
  private readonly PATHUF: string = '/estados';

  constructor(private http: HttpClient) {}

  getAllEstados(): Observable<Estado[]> {
    return this.http.get<Estado[]>(API_CONFIG.baseUrl + this.PATHUF);
  }

  getAllEstadoCidades(idEstado: number): Observable<Cidade[]> {
    return this.http.get<Cidade[]>(
      `${API_CONFIG.baseUrl + this.PATHUF}/${idEstado}/cidades`,
    );
  }
}
