import { Component, OnInit } from '@angular/core';
import {
  TipoGrupoServico,
  TipoGrupoServicoService,
} from 'src/app/modules/shared';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-listar-tipo-grupo',
  templateUrl: './listar-tipo-grupo.component.html',
  styleUrls: ['./listar-tipo-grupo.component.css'],
})
export class ListarTipoGrupoComponent implements OnInit {
  private subscriptions: Subscription[] = [];

  tipoGrupoServicos: TipoGrupoServico[] = [];

  constructor(private tipoGrupoServicoService: TipoGrupoServicoService) {}

  ngOnInit(): void {
    this.carregaDadosTipoGrupoServicos();
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach((subscription) => subscription.unsubscribe());
  }

  carregaDadosTipoGrupoServicos(): void {
    this.subscriptions.push(
      this.tipoGrupoServicoService.getAll().subscribe(
        (data) => {
          this.tipoGrupoServicos = data;
        },
        (err) => console.log(err),
      ),
    );
  }
}
