import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import {
  OrdemServicoHasGrupoServico,
  OrdemServiceGrupoServiceDTO,
} from '../../models';
import { API_CONFIG } from '../../config';

@Injectable({
  providedIn: 'root',
})
export class OrdemServicoGrupoServicoService {
  private readonly PATH: string = '/ordemServicogrupoServico';

  constructor(private http: HttpClient) {}

  getAll(): Observable<OrdemServicoHasGrupoServico[]> {
    return this.http.get<OrdemServicoHasGrupoServico[]>(
      API_CONFIG.baseUrl + this.PATH,
    );
  }

  findById(
    idOS: number,
    idGS: number,
  ): Observable<OrdemServicoHasGrupoServico> {
    return this.http.get<OrdemServicoHasGrupoServico>(
      `${API_CONFIG.baseUrl + this.PATH}/${idOS}/${idGS}`,
    );
  }

  insert(
    ordemServicoHasGrupoServico: OrdemServiceGrupoServiceDTO,
  ): Observable<any> {
    return this.http.post(
      API_CONFIG.baseUrl + this.PATH,
      ordemServicoHasGrupoServico,
    );
  }

  delete(idOS: number, idGS: number): Observable<any> {
    return this.http.delete(
      `${API_CONFIG.baseUrl + this.PATH}/${idOS}/${idGS}`,
    );
  }
}
