import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { GrupoServico } from '../../models';
import { API_CONFIG } from '../../config';

@Injectable({
  providedIn: 'root',
})
export class GrupoServicoService {
  private readonly PATH: string = '/grupoServicos';

  constructor(private http: HttpClient) {}

  getAll(): Observable<GrupoServico[]> {
    return this.http.get<GrupoServico[]>(API_CONFIG.baseUrl + this.PATH);
  }

  insert(grupoServico: GrupoServico): Observable<any> {
    return this.http.post(API_CONFIG.baseUrl + this.PATH, grupoServico);
  }

  findById(id: number): Observable<GrupoServico> {
    return this.http.get<GrupoServico>(
      `${API_CONFIG.baseUrl + this.PATH}/${id}`,
    );
  }

  update(grupoServico: GrupoServico): Observable<any> {
    return this.http.put(
      `${API_CONFIG.baseUrl + this.PATH}/${grupoServico.id}`,
      grupoServico,
    );
  }
}
