import { Component, OnInit } from '@angular/core';
import {
  OrdemServico,
  OrdemServicoService,
  OrdemServicoHasGrupoServico,
  OrdemServicoGrupoServicoService,
  GerarRelatorio,
} from 'src/app/modules/shared';
import { Subscription } from 'rxjs';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import * as html2pdf from 'html2pdf.js';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-listar',
  templateUrl: './listar.component.html',
  styleUrls: ['./listar.component.css'],
})
export class ListarComponent implements OnInit {
  private subscriptions: Subscription[] = [];

  ordemServicos: OrdemServico[] = [];

  ordemServicosRelatorio: OrdemServico[] = [];

  ordemServicosGrupoServico: OrdemServicoHasGrupoServico[] = [];

  oSGS = {} as OrdemServicoHasGrupoServico;

  startDate = new Date();

  mostrarRelatorioImpressao = false;

  form: FormGroup;

  relatorioFormulario = false;

  valorTotal = 0;

  dadosRelatorio = {} as GerarRelatorio;

  nomeRelatorio: string;

  constructor(
    private ordemServicoService: OrdemServicoService,
    private fb: FormBuilder,
    private ordemServicoGrupoServicoService: OrdemServicoGrupoServicoService,
  ) {}

  ngOnInit(): void {
    this.criarForm();
    this.carregaDadosOrdemServicos();
    this.carregaDadosOSGS();
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach((subscription) => subscription.unsubscribe());
  }

  criarForm(): void {
    this.form = this.fb.group({
      nomerelatorio: ['', Validators.required],
      datainicio: ['', Validators.required],
      datafim: ['', Validators.required],
    });
  }

  carregaDadosOrdemServicos(): void {
    this.subscriptions.push(
      this.ordemServicoService.getAll().subscribe(
        (data) => {
          this.ordemServicos = data;
        },
        (err) => {
          const msg = err.error;
          Swal.fire({
            title: msg,
            icon: 'error',
            showConfirmButton: false,
            timer: 3000,
          });
        },
      ),
    );
  }

  habilitarDesabilitarFormRelatorio(): void {
    this.relatorioFormulario = !this.relatorioFormulario;
    this.mostrarRelatorioImpressao = false;
  }

  gerarRelatorio(): void {
    if (this.form.invalid) {
      return;
    }

    this.dadosRelatorio = this.form.value;
    this.nomeRelatorio = this.dadosRelatorio.nomerelatorio;

    if (this.dadosRelatorio.datainicio > this.dadosRelatorio.datafim) {
      const msg = 'Data inicio no pode ser maior que data fim!';
      Swal.fire({
        title: msg,
        icon: 'error',
        showConfirmButton: false,
        timer: 3000,
      });
      return;
    }
    this.valorTotal = 0;
    this.ordemServicosRelatorio = [];
    this.ordemServicos.forEach((x) => {
      x.valorTotalOS = 0;
      x.data = new Date(x.data);

      if (
        x.data >= this.dadosRelatorio.datainicio &&
        x.data <= this.dadosRelatorio.datafim
      ) {
        x.grupoServico.forEach((gs) => {
          this.oSGS = this.ordemServicosGrupoServico.find((osgs) => {
            return (
              osgs.id.grupoServico.id === gs.id &&
              osgs.id.ordemServico.id === x.id
            );
          });

          if (this.oSGS) {
            x.valorTotalOS += this.oSGS.quantidade * this.oSGS.valor;
          }
        });
        this.valorTotal += x.valorTotalOS;
        this.ordemServicosRelatorio.push(x);
      }
    });

    const msg = 'Relatorio gerado com sucesso!';
    Swal.fire({
      title: msg,
      icon: 'success',
      showConfirmButton: false,
      timer: 2000,
    });

    this.mostrarRelatorioImpressao = true;
  }

  gerarPDF(): void {
    const content: Element = document.getElementById('relatoriomensal');

    const options = {
      margin: 1,
      filename: `${this.nomeRelatorio}.pdf`,
      image: { type: 'jpeg' },
      html2canvas: { scale: 2 },
      jsPDF: { orientation: 'portrait' },
    };

    html2pdf().set(options).from(content).save();
  }

  carregaDadosOSGS(): void {
    this.subscriptions.push(
      this.ordemServicoGrupoServicoService.getAll().subscribe(
        (data) => {
          this.ordemServicosGrupoServico = data;
        },
        (err) => {
          const msg = err.error;
          Swal.fire({
            title: msg,
            icon: 'error',
            showConfirmButton: false,
            timer: 3000,
          });
        },
      ),
    );
  }
}
