import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import {
  UnidadeGrupoServico,
  ErroGeral,
  ErroDTO,
  GrupoServico,
  UnidadeGrupoServicoService,
  GrupoServicoService,
} from 'src/app/modules/shared';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import Swal from 'sweetalert2';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-atualizar',
  templateUrl: './atualizar.component.html',
  styleUrls: ['./atualizar.component.css'],
})
export class AtualizarComponent implements OnInit {
  private subscriptions: Subscription[] = [];

  unidadeGrupoServicos: UnidadeGrupoServico[] = [];

  unidadeGrupoServico = {} as UnidadeGrupoServico;

  erroGeral = {} as ErroGeral;

  erroDTO = {} as ErroDTO;

  form: FormGroup;

  grupoServico: GrupoServico;

  grupoServicoId: string;

  constructor(
    private uGSService: UnidadeGrupoServicoService,
    private toast: ToastrService,
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private grupoServicoService: GrupoServicoService,
  ) {}

  ngOnInit(): void {
    this.grupoServicoId = this.route.snapshot.paramMap.get('gsid');
    this.criarForm();
    this.carregaDadosUGS();
    this.carregaDadosGrupoServico();
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach((subscription) => subscription.unsubscribe());
  }

  criarForm(): void {
    this.form = this.fb.group({
      codigoSapItem: ['', Validators.required],
      descSapItem: ['', Validators.required],
      valorUnitario: ['', Validators.required],
      unidadeGrupoServico: ['', Validators.required],
      descServico: ['', Validators.required],
    });
  }

  carregaDadosGrupoServico(): void {
    this.subscriptions.push(
      this.grupoServicoService.findById(Number(this.grupoServicoId)).subscribe(
        (data) => {
          this.grupoServico = data;
          this.populandoFomulario();
        },
        (err) => console.log(err),
      ),
    );
  }

  carregaDadosUGS(): void {
    this.subscriptions.push(
      this.uGSService.getAll().subscribe(
        (data) => {
          this.unidadeGrupoServicos = data;
        },
        (err) => console.log(err),
      ),
    );
  }

  atualizarGrupoServico(): void {
    if (this.form.invalid) {
      this.toast.error('Formulrio com campos invalidos', 'Erro!', {
        timeOut: 4000,
      });
      this.toucheCamposFormulario();
      return;
    }

    this.grupoServico = this.form.value;
    const unidadeGrupoServicoId = Number(
      this.form.get('unidadeGrupoServico').value,
    );

    this.grupoServico.unidadeGrupoServico = this.unidadeGrupoServicos.find(
      (x) => x.id === unidadeGrupoServicoId,
    );
    this.grupoServico.id = Number(this.grupoServicoId);
    this.subscriptions.push(
      this.grupoServicoService.update(this.grupoServico).subscribe(
        (data) => {
          const msg = 'Grupo de servio atualizado com sucesso!';

          Swal.fire({
            title: msg,
            icon: 'success',
            showConfirmButton: false,
            timer: 2000,
          });
          setTimeout(() => {
            this.refresh();
          }, 1900);
        },
        (err) => console.log(err),
      ),
    );
  }

  toucheCamposFormulario(): void {
    this.form.get('codigoSapItem').markAsTouched();
    this.form.get('descSapitem').markAsTouched();
    this.form.get('valorUnitario').markAsTouched();
    this.form.get('unidadeGrupoServico').markAsTouched();
    this.form.get('servico').markAsTouched();
  }

  populandoFomulario(): void {
    this.form.patchValue({
      codigoSapItem: this.grupoServico.codigoSapItem,
      descSapitem: this.grupoServico.descSapItem,
      valorUnitario: this.grupoServico.valorUnitario,
      unidadeGrupoServico: this.grupoServico.unidadeGrupoServico.id,
    });
  }

  refresh(): void {
    window.location.reload();
  }
}
