import { Component, OnInit } from '@angular/core';
import {
  Cliente,
  ClienteService,
  ErroGeral,
  ErroDTO,
  Estado,
  Cidade,
  EnderecoService,
  NewClienteDTO,
} from 'src/app/modules/shared';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import Swal from 'sweetalert2';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-cadastrar',
  templateUrl: './cadastrar.component.html',
  styleUrls: ['./cadastrar.component.css'],
})
export class CadastrarComponent implements OnInit {
  private subscriptions: Subscription[] = [];

  erroGeral = {} as ErroGeral;

  erroDTO = {} as ErroDTO;

  newClienteDTO = {} as NewClienteDTO;

  estados: Estado[];

  cidades: Cidade[];

  form: FormGroup;

  mostrarCidade = false;

  mostrarEndereco = false;

  idCidade = null;

  constructor(
    private fb: FormBuilder,
    private spinner: NgxSpinnerService,
    private clienteService: ClienteService,
    private enderecoService: EnderecoService,
    private toast: ToastrService,
  ) {}

  ngOnInit(): void {
    this.criarForm();
    this.carregaEstados();
  }

  criarForm(): void {
    this.form = this.fb.group({
      codigoSupergas: ['', Validators.required],
      nome: ['', Validators.required],
      cnpj: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
      telefone: ['', Validators.required],
      observacoes: ['', Validators.required],
      estado: ['', Validators.required],
      cidade: ['', Validators.required],
      cep: ['', Validators.required],
      logradouro: ['', Validators.required],
      numero: ['', Validators.required],
      bairro: [''],
      complemento: [''],
    });

    this.form.patchValue({ observacoes: 'Sem Observaes' });
  }

  carregaEstados(): void {
    this.subscriptions.push(
      this.enderecoService.getAllEstados().subscribe(
        (data) => {
          this.estados = data;
        },
        (err) => console.log(err),
      ),
    );
  }

  estadoSelecionado(id: number): void {
    if (Number(id) === 100) {
      this.mostrarCidade = false;
      this.mostrarEndereco = false;
      return;
    }
    this.carregaCidades(id);
    this.mostrarCidade = true;
    console.log(id);
  }

  cidadeSelecionada(id: number): void {
    if (Number(id) === 100) {
      this.mostrarEndereco = false;
      return;
    }
    this.idCidade = id;
    this.mostrarEndereco = true;
  }

  carregaCidades(id: number): void {
    this.subscriptions.push(
      this.enderecoService.getAllEstadoCidades(id).subscribe(
        (data) => {
          this.cidades = data;
        },
        (err) => console.log(err),
      ),
    );
  }

  cadastrarCliente(): void {
    this.spinner.show();

    if (this.form.invalid) {
      this.spinner.hide();
      this.toast.error('Formulrio com campos invalidos', 'Erro!', {
        timeOut: 4000,
      });
      return;
    }

    this.newClienteDTO = this.form.value;
    this.newClienteDTO.cidadeId = Number(this.form.get('cidade').value);

    this.subscriptions.push(
      this.clienteService.insert(this.newClienteDTO).subscribe(
        (data) => {
          const msg = 'Cliente cadastrado com sucesso!';
          this.spinner.hide();
          Swal.fire({
            title: msg,
            icon: 'success',
            showConfirmButton: false,
            timer: 2000,
          });
          setTimeout(() => {
            this.refresh();
          }, 1900);
        },
        (err) => console.log(err),
      ),
    );
    this.spinner.hide();
  }

  refresh(): void {
    window.location.reload();
  }
}
