import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { TipoGrupoServico } from '../../models';
import { API_CONFIG } from '../../config';

@Injectable({
  providedIn: 'root',
})
export class TipoGrupoServicoService {
  private readonly PATH: string = '/tipoGrupoServicos';

  constructor(private http: HttpClient) {}

  getAll(): Observable<TipoGrupoServico[]> {
    return this.http.get<TipoGrupoServico[]>(API_CONFIG.baseUrl + this.PATH);
  }
}
