import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import {
  OrdemServicoService,
  ClienteService,
  OrdemServico,
  Cliente,
  ErroGeral,
  ErroDTO,
  OrdemServiceGrupoServiceDTO,
  GrupoServico,
  UnidadeGrupoServico,
  TipoGrupoServico,
  GrupoServicoService,
  UnidadeGrupoServicoService,
  TipoGrupoServicoService,
  OrdemServicoGrupoServicoService,
} from 'src/app/modules/shared';
import Swal from 'sweetalert2';

import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-atualizar',
  templateUrl: './atualizar.component.html',
  styleUrls: ['./atualizar.component.css'],
})
export class AtualizarComponent implements OnInit {
  // VARIAVEIS CLASSE PRINCIPAL

  erroGeral = {} as ErroGeral;

  erroDTO = {} as ErroDTO;

  startDate = new Date();

  private subscriptions: Subscription[] = [];

  ordemServico = {} as OrdemServico;

  ordemServicoCadastrada = {} as OrdemServico;

  ordemServicoGrupoServico = {} as OrdemServiceGrupoServiceDTO;

  form: FormGroup;

  // VARIAVEIS CLIENTE

  clienteSelecionado = false;

  dadosCliente = false;

  clientes: Cliente[] = [];

  cliente = {} as Cliente;

  clienteFiltrados: Cliente[] = [];

  // VARIAVEIS GRUPO DE SERVIO

  grupoServicoSelecionado = false;

  dadosGrupoServico = false;

  grupoServicos: GrupoServico[] = [];

  grupoServicosFiltrados: GrupoServico[] = [];

  grupoServico = {} as GrupoServico;

  formGrupoServico: FormGroup;

  unidadeGrupoServicos: UnidadeGrupoServico[] = [];

  unidadeGrupoServico = {} as UnidadeGrupoServico;

  tipoGrupoServicos: TipoGrupoServico[] = [];

  tipoGrupoServico = {} as TipoGrupoServico;

  totalServicos = 0;

  ordemServicoId: number;

  constructor(
    private route: ActivatedRoute,
    private clienteService: ClienteService,
    private grupoServicoService: GrupoServicoService,
    private uGSService: UnidadeGrupoServicoService,
    private tGSService: TipoGrupoServicoService,
    private toast: ToastrService,
    private router: Router,
    private fb: FormBuilder,
    private ordemServicoService: OrdemServicoService,
    private ordemServicoGrupoServicoService: OrdemServicoGrupoServicoService,
  ) {}

  ngOnInit(): void {
    this.ordemServicoId = Number(this.route.snapshot.paramMap.get('osid'));
    this.criarForm();
    this.carregarClientes();
    this.carregarGrupoServico();
    this.criarFormGrupoServico();
    this.carregaDadosUGS();
    this.carregaDadosTGS();
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach((subscription) => subscription.unsubscribe());
  }

  criarForm(): void {
    this.form = this.fb.group({
      codigoSupergas: ['', Validators.required],
      data: ['', Validators.required],
      cliente: ['', Validators.required],
    });
  }

  cadastrarOrdemServico(): void {
    if (this.form.invalid || this.formGrupoServico.invalid) {
      this.toast.error('Formulrio com campos invalidos', 'Erro!', {
        timeOut: 4000,
      });
      this.toucheCamposFormulario();
      return;
    }

    this.ordemServico.codigoSupergas = Number(
      this.form.get('codigoSupergas').value,
    );
    this.ordemServico.data = this.form.get('data').value;
    this.ordemServico.cliente = this.cliente;
    this.ordemServico.status = 1;

    if (this.ordemServicoCadastrada.id) {
      this.adicionarGrupoDeServioOSAtual();
    } else {
      this.subscriptions.push(
        this.ordemServicoService.insert(this.ordemServico).subscribe(
          (data) => {
            const msg = 'Ordem de servio cadastrado com sucesso!';
            this.ordemServicoCadastrada = data;
            Swal.fire({
              title: msg,
              icon: 'success',
              showConfirmButton: false,
              timer: 2000,
            });
            this.adicionarGrupoDeServioOSAtual();
          },
          (err) => {
            const msg = err.error;
            Swal.fire({
              title: msg,
              icon: 'error',
              showConfirmButton: false,
              timer: 2000,
            });
          },
        ),
      );
    }
  }

  toucheCamposFormulario(): void {
    this.form.get('codigoSupergas').markAsTouched();
    this.form.get('data').markAsTouched();
    this.form.get('cliente').markAsTouched();
  }

  refresh(): void {
    window.location.reload();
  }

  // ///// FUES DE CLIENTE  \\\\\\\\\

  filtraUsuarioPeloNome(nome: string): void {
    this.clienteSelecionado = true;
    this.dadosCliente = false;
    if (!nome) {
      this.clienteFiltrados = this.clientes;
    } else {
      this.clienteFiltrados = this.clientes.filter((x) =>
        x.nome.trim().toLowerCase().includes(nome.trim().toLowerCase()),
      );
    }
  }

  dadosClienteSelecionado(id: number): void {
    this.cliente = this.clientes.find((x) => x.id === id);

    if (!this.cliente) {
      const msg = 'Erro ao carregar cliente';
      Swal.fire({
        title: msg,
        icon: 'error',
        showConfirmButton: false,
        timer: 2000,
      });
    }

    this.populandoFomularioCliente();
    this.clienteSelecionado = false;
  }

  populandoFomularioCliente(): void {
    this.dadosCliente = true;
    this.form.patchValue({
      cliente: this.cliente.nome,
      data: this.ordemServicoCadastrada.data,
    });
  }

  carregarClientes(): void {
    this.subscriptions.push(
      this.clienteService.getAll().subscribe(
        (data) => {
          this.clientes = data;
          this.carregarDadosOsAtual();
        },
        (err) => {
          const msg = err.error;
          Swal.fire({
            title: msg,
            icon: 'error',
            showConfirmButton: false,
            timer: 2000,
          });
        },
      ),
    );
  }

  // ///// FUES DE GRUPO DE SERVIO  \\\\\\\\\

  criarFormGrupoServico(): void {
    this.formGrupoServico = this.fb.group({
      codigoSapItem: ['', Validators.required],
      descSapItem: ['', Validators.required],
      valorUnitario: ['', Validators.required],
      quantidade: ['', Validators.required],
      unidadeGrupoServico: ['', Validators.required],
      descServico: ['', Validators.required],
      tipoGrupoServico: ['', Validators.required],
    });
  }

  limparCodSupergas(): void {
    this.form.patchValue({
      codigoSupergas: null,
    });
  }

  filtraGrupoServicoCodSupergas(codSap: string): void {
    this.grupoServicoSelecionado = true;
    this.dadosGrupoServico = false;
    if (!codSap) {
      this.grupoServicosFiltrados = this.grupoServicos;
    } else {
      this.grupoServicosFiltrados = this.grupoServicos.filter((x) =>
        x.codigoSapItem
          .toString()
          .trim()
          .toLowerCase()
          .includes(codSap.toLowerCase().trim()),
      );
    }
  }

  carregarGrupoServico(): void {
    this.subscriptions.push(
      this.grupoServicoService.getAll().subscribe(
        (data) => {
          this.grupoServicos = data;
        },
        (err) => {
          const msg = err.error;
          Swal.fire({
            title: msg,
            icon: 'error',
            showConfirmButton: false,
            timer: 2000,
          });
        },
      ),
    );
  }

  dadosGrupoServicoSelecionado(id: number): void {
    this.grupoServico = this.grupoServicos.find((x) => x.id === id);
    if (!this.grupoServico) {
      const msg = 'Erro ao carregar cliente';
      Swal.fire({
        title: msg,
        icon: 'error',
        showConfirmButton: false,
        timer: 2000,
      });
    }
    this.populandoCodGrupoServico();
    this.grupoServicoSelecionado = false;
  }

  populandoCodGrupoServico(): void {
    this.dadosGrupoServico = true;
    this.form.patchValue({
      codigoSupergas: this.grupoServico.codigoSapItem,
    });

    this.populandoFormularioGrupoServico();
  }

  populandoFormularioGrupoServico(): void {
    this.formGrupoServico.patchValue({
      codigoSapItem: this.grupoServico.codigoSapItem,
      descSapItem: this.grupoServico.descSapItem,
      valorUnitario: this.grupoServico.valorUnitario,
      quantidade: 1,
      unidadeGrupoServico: this.grupoServico.unidadeGrupoServico.id,
      descServico: this.grupoServico.descServico,
      tipoGrupoServico: this.grupoServico.tipoGrupoServico.id,
    });
  }

  carregaDadosUGS(): void {
    this.subscriptions.push(
      this.uGSService.getAll().subscribe(
        (data) => {
          this.unidadeGrupoServicos = data;
        },
        (err) => {
          const msg = err.error;
          Swal.fire({
            title: msg,
            icon: 'error',
            showConfirmButton: false,
            timer: 2000,
          });
        },
      ),
    );
  }

  carregaDadosTGS(): void {
    this.subscriptions.push(
      this.tGSService.getAll().subscribe(
        (data) => {
          this.tipoGrupoServicos = data;
        },
        (err) => {
          const msg = err.error;
          Swal.fire({
            title: msg,
            icon: 'error',
            showConfirmButton: false,
            timer: 2000,
          });
        },
      ),
    );
  }

  adicionarGrupoDeServioOSAtual(): void {
    this.ordemServicoGrupoServico.grupoServicoId = this.grupoServico.id;
    this.ordemServicoGrupoServico.ordemServicoId = this.ordemServicoCadastrada.id;
    this.ordemServicoGrupoServico.quantidade = this.formGrupoServico.get(
      'quantidade',
    ).value;
    this.ordemServicoGrupoServico.valor = this.grupoServico.valorUnitario;

    this.subscriptions.push(
      this.ordemServicoGrupoServicoService
        .insert(this.ordemServicoGrupoServico)
        .subscribe(
          (data) => this.carregarDadosOsAtual(),
          (err) => {
            const msg = err.error;
            Swal.fire({
              title: msg,
              icon: 'error',
              showConfirmButton: false,
              timer: 2000,
            });
          },
        ),
    );
  }

  carregarDadosOsAtual(): void {
    this.subscriptions.push(
      this.ordemServicoService.findById(this.ordemServicoId).subscribe(
        (data) => {
          this.ordemServicoCadastrada = data;
          if (this.ordemServicoCadastrada.status === 0) {
            this.router.navigate([
              `/order-service/visualizar/${this.ordemServicoCadastrada.id}`,
            ]);
          }
          this.quandoServicoGrupoServico();
          this.dadosClienteSelecionado(this.ordemServicoCadastrada.cliente.id);
        },
        (err) => {
          const msg = err.error;
          Swal.fire({
            title: msg,
            icon: 'error',
            showConfirmButton: false,
            timer: 2000,
          });
        },
      ),
    );
    this.limparCodSupergas();
  }

  quandoServicoGrupoServico(): void {
    const idOS = this.ordemServicoCadastrada.id;
    this.totalServicos = 0;

    this.ordemServicoCadastrada.grupoServico.forEach((x) => {
      this.ordemServicoGrupoServicoService.findById(idOS, x.id).subscribe(
        (data) => {
          x.quantidade = data.quantidade;
          x.valorUnitario = data.valor;
          this.totalServicos += data.quantidade * data.valor;
          return x;
        },
        (err) => {
          const msg = err.error;
          Swal.fire({
            title: msg,
            icon: 'error',
            showConfirmButton: false,
            timer: 2000,
          });
        },
      );
    });
  }

  finalizarOrdemDeServico(): void {
    this.ordemServicoCadastrada.status = 0;
    this.subscriptions.push(
      this.ordemServicoService.update(this.ordemServicoCadastrada).subscribe(
        (data) => {
          const msg = 'Ordem de servio fechada com sucesso';
          Swal.fire({
            title: msg,
            icon: 'success',
            showConfirmButton: false,
            timer: 2000,
          });

          setTimeout(() => {
            this.refresh();
          }, 1900);
        },
        (err) => {
          const msg = err.error;
          Swal.fire({
            title: msg,
            icon: 'error',
            showConfirmButton: false,
            timer: 2000,
          });
        },
      ),
    );
  }

  removerItemServicoAtual(idOS: number, idGS: number): void {
    this.subscriptions.push(
      this.ordemServicoGrupoServicoService.delete(idOS, idGS).subscribe(
        (data) => this.carregarDadosOsAtual(),
        (err) => {
          const msg = err.error;
          Swal.fire({
            title: msg,
            icon: 'error',
            showConfirmButton: false,
            timer: 2000,
          });
        },
      ),
    );
  }

  cadastrarGrupoServico(): void {
    if (this.form.invalid) {
      this.toast.error('Formulrio com campos invalidos', 'Erro!', {
        timeOut: 4000,
      });
      this.toucheCamposFormulario();
      return;
    }

    this.grupoServico = this.form.value;
    const unidadeGrupoServicoId = Number(
      this.form.get('unidadeGrupoServico').value,
    );
    const tipoGrupoServicoId = Number(this.form.get('tipoGrupoServico').value);

    this.grupoServico.unidadeGrupoServico = this.unidadeGrupoServicos.find(
      (x) => x.id === unidadeGrupoServicoId,
    );

    this.grupoServico.tipoGrupoServico = this.tipoGrupoServicos.find(
      (x) => x.id === tipoGrupoServicoId,
    );

    this.subscriptions.push(
      this.grupoServicoService.insert(this.grupoServico).subscribe(
        (data) => {
          const msg = 'Grupo de servio cadastrado com sucesso!';

          Swal.fire({
            title: msg,
            icon: 'success',
            showConfirmButton: false,
            timer: 2000,
          });
        },
        (err) => {
          const msg = err.error;
          Swal.fire({
            title: msg,
            icon: 'error',
            showConfirmButton: false,
            timer: 2000,
          });
        },
      ),
    );
  }
}
