import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Cliente, NewClienteDTO } from '../../models';
import { API_CONFIG } from '../../config';

@Injectable({
  providedIn: 'root',
})
export class ClienteService {
  private readonly PATH: string = '/clientes';

  constructor(private http: HttpClient) {}

  getAll(): Observable<Cliente[]> {
    return this.http.get<Cliente[]>(API_CONFIG.baseUrl + this.PATH);
  }

  insert(clienteDTO: NewClienteDTO): Observable<any> {
    return this.http.post(API_CONFIG.baseUrl + this.PATH, clienteDTO);
  }

  update(cliente: Cliente): Observable<any> {
    return this.http.put(
      `${API_CONFIG.baseUrl + this.PATH}/${cliente.id}`,
      cliente,
    );
  }

  findById(id: number): Observable<Cliente> {
    return this.http.get<Cliente>(`${API_CONFIG.baseUrl + this.PATH}/${id}`);
  }
}
