import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { JwtHelperService } from '@auth0/angular-jwt';
import { Login, Forgot, TokenUser } from '../../models';
import { API_CONFIG } from '../../config';
import { StorangeService } from '../storange.service';

@Injectable()
export class LoginService {
  private readonly PATH: string = '/auth';

  helper = new JwtHelperService();

  constructor(
    private http: HttpClient,
    private localStorangeService: StorangeService,
  ) {}

  logar(login: Login): Observable<any> {
    return this.http.post(`${API_CONFIG.baseUrl}/login`, login, {
      observe: 'response',
      responseType: 'text',
    });
  }

  successLogin(authorizationValue: string): void {
    const tok = authorizationValue.substring(7);

    const { sub } = this.helper.decodeToken(tok);
    const tokenUser: TokenUser = {
      token: tok,
      email: sub,
    };

    this.localStorangeService.setLocalTokenUser(tokenUser);
  }

  logout(): void {
    this.localStorangeService.setLocalTokenUser(null);
  }

  forgot(email: Forgot): Observable<any> {
    return this.http.post(`${API_CONFIG.baseUrl + this.PATH}/forgot`, email);
  }
}
