import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { OrdemServico } from '../../models';
import { API_CONFIG } from '../../config';

@Injectable({
  providedIn: 'root',
})
export class OrdemServicoService {
  private readonly PATH: string = '/ordemServicos';

  constructor(private http: HttpClient) {}

  getAll(): Observable<OrdemServico[]> {
    return this.http.get<OrdemServico[]>(API_CONFIG.baseUrl + this.PATH);
  }

  insert(ordemServico: OrdemServico): Observable<any> {
    return this.http.post(API_CONFIG.baseUrl + this.PATH, ordemServico);
  }

  findById(id: number): Observable<OrdemServico> {
    return this.http.get<OrdemServico>(
      `${API_CONFIG.baseUrl + this.PATH}/${id}`,
    );
  }

  update(ordemServico: OrdemServico): Observable<any> {
    return this.http.put(
      `${API_CONFIG.baseUrl + this.PATH}/${ordemServico.id}`,
      ordemServico,
    );
  }
}
