// https://viacep.com.br/ws/72015035/json
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { DadosCEP } from '../../models';

@Injectable({
  providedIn: 'root',
})
export class ConsultaCEPService {
  private readonly PATH: string = 'https://viacep.com.br/ws/';

  constructor(private http: HttpClient) {}

  getDadosCep(cep: string): Observable<DadosCEP> {
    return this.http.get<DadosCEP>(`${this.PATH + cep}/json`);
  }
}
