import { NgModule } from '@angular/core';

import { RouterModule, Routes } from '@angular/router';

import {
  AtualizarComponent,
  CadastrarComponent,
  ListarComponent,
  ListarTipoGrupoComponent,
  ListarUnidadeComponent,
  GroupServiceComponent,
} from './components';
import { AutenticaRotaSerivce, AuthGuardService } from '../../shared';

export const GroupServiceRoutes: Routes = [
  {
    path: 'group-service',
    component: GroupServiceComponent,
    canActivate: [AuthGuardService],

    children: [
      {
        path: 'pesquisar',
        component: ListarComponent,
        canActivate: [AutenticaRotaSerivce],
      },
      {
        path: 'pesquisar-unidade',
        component: ListarUnidadeComponent,
        canActivate: [AutenticaRotaSerivce],
      },
      {
        path: 'pesquisar-tipo-grupo',
        component: ListarTipoGrupoComponent,
        canActivate: [AutenticaRotaSerivce],
      },
      {
        path: 'cadastrar',
        component: CadastrarComponent,
        canActivate: [AutenticaRotaSerivce],
      },
      { path: 'atualizar/:gsid', component: AtualizarComponent },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(GroupServiceRoutes)],

  exports: [RouterModule],
})
export class GroupServiceRoutingModule {}
