import { Component, OnInit } from '@angular/core';
import {
  UnidadeGrupoServico,
  UnidadeGrupoServicoService,
} from 'src/app/modules/shared';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-listar-unidade',
  templateUrl: './listar-unidade.component.html',
  styleUrls: ['./listar-unidade.component.css'],
})
export class ListarUnidadeComponent implements OnInit {
  private subscriptions: Subscription[] = [];

  unidadeGrupoServico: UnidadeGrupoServico[] = [];

  constructor(private uGSService: UnidadeGrupoServicoService) {}

  ngOnInit(): void {
    this.carregaDadosUGS();
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach((subscription) => subscription.unsubscribe());
  }

  public loadScript(url: string): void {
    const body = <HTMLDivElement>document.body;
    const script = document.createElement('script');
    script.innerHTML = '';
    script.src = url;
    script.async = false;
    script.defer = true;
    body.appendChild(script);
  }

  carregaDadosUGS(): void {
    this.subscriptions.push(
      this.uGSService.getAll().subscribe(
        (data) => {
          this.unidadeGrupoServico = data;
        },
        (err) => console.log(err),
      ),
    );
  }
}
