import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { GrupoServico, GrupoServicoService } from 'src/app/modules/shared';

@Component({
  selector: 'app-listar',
  templateUrl: './listar.component.html',
  styleUrls: ['./listar.component.css'],
})
export class ListarComponent implements OnInit {
  private subscriptions: Subscription[] = [];

  grupoServicos: GrupoServico[] = [];

  constructor(private grupoServicoService: GrupoServicoService) {}

  ngOnInit(): void {
    this.carregaDadosGrupoServicos();
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach((subscription) => subscription.unsubscribe());
  }

  carregaDadosGrupoServicos(): void {
    this.subscriptions.push(
      this.grupoServicoService.getAll().subscribe(
        (data) => {
          this.grupoServicos = data;
          console.log(this.grupoServicos);
        },
        (err) => console.log(err),
      ),
    );
  }
}
