import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import * as html2pdf from 'html2pdf.js';
import {
  OrdemServicoService,
  OrdemServico,
  OrdemServicoGrupoServicoService,
} from 'src/app/modules/shared';
import { Subscription } from 'rxjs';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-visualizar',
  templateUrl: './visualizar.component.html',
  styleUrls: ['./visualizar.component.css'],
})
export class VisualizarComponent implements OnInit {
  private subscriptions: Subscription[] = [];

  ordemServicoId: number;

  ordemServico = {} as OrdemServico;

  relatorio = 'OrdemDeServico';

  totalServicos: number;

  constructor(
    private route: ActivatedRoute,
    private ordemServicoService: OrdemServicoService,
    private ordemServicoGrupoServicoService: OrdemServicoGrupoServicoService,
  ) {}

  ngOnInit(): void {
    this.ordemServicoId = Number(this.route.snapshot.paramMap.get('osid'));
    this.carregaDadosOS();
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach((subscription) => subscription.unsubscribe());
  }

  carregaDadosOS(): void {
    this.subscriptions.push(
      this.ordemServicoService.findById(this.ordemServicoId).subscribe(
        (data) => {
          this.ordemServico = data;
          this.quandoServicoGrupoServico();
        },
        (err) => console.error(err),
      ),
    );
  }

  quandoServicoGrupoServico(): void {
    const idOS = this.ordemServicoId;
    this.totalServicos = 0;

    this.ordemServico.grupoServico.forEach((x) => {
      this.ordemServicoGrupoServicoService.findById(idOS, x.id).subscribe(
        (data) => {
          x.quantidade = data.quantidade;
          x.valorUnitario = data.valor;
          this.totalServicos += data.quantidade * data.valor;
          x.subTotal = 0;
          x.subTotal = data.quantidade * data.valor;
          return x;
        },
        (err) => {
          const msg = err.error;
          Swal.fire({
            title: msg,
            icon: 'error',
            showConfirmButton: false,
            timer: 2000,
          });
        },
      );
    });
  }

  gerarPDF(): void {
    const options = {
      filename: `${this.relatorio}.pdf`,
      image: { type: 'jpeg' },
      html2canvas: {},
      jsPDF: { orientation: 'portrait' },
    };

    const content: Element = document.getElementById('relatorioUsuario');

    html2pdf().from(content).set(options).save();
  }
}
