import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { UnidadeGrupoServico } from '../../models';
import { API_CONFIG } from '../../config';

@Injectable({
  providedIn: 'root',
})
export class UnidadeGrupoServicoService {
  private readonly PATH: string = '/unidadeGrupoServicos';

  constructor(private http: HttpClient) {}

  getAll(): Observable<UnidadeGrupoServico[]> {
    return this.http.get<UnidadeGrupoServico[]>(API_CONFIG.baseUrl + this.PATH);
  }
}
